@charset "UTF-8";

.top-btn {
  position: fixed;
  right: 24px;
  bottom: 30px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: none;
  background: #333;
  color: #fff;
  font-size: 22px;
  cursor: pointer;
  z-index: 999;

  display: flex;
  align-items: center;
  justify-content: center;

  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.top-btn:hover {
  background: #000;
  transform: translateY(-4px);
}
