@charset "UTF-8";

.input_item{
  max-width: 878px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input_item label{ 
  display: block;
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin: 12px 0;
  text-align: left;
  padding-left: 20px;
 }
.input_item p{
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}
.input_item input{
  width: 98%;
  height: 48px;
  padding: 0 16px 0;
  color: #333;
  border: none;
  background: #efefef;
  margin: 0 auto;
}
 
.product_list{
  max-width: 878px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}
.product_list li{
  width: 48%;
  height: 100%;
   overflow: hidden;
 }
.product_list img{
 width: 100%;

}
.preview {
  margin: 10px 0;
}

.preview img {
  width: 120px;
  height: auto;
  border-radius: 6px;
  border: 1px solid #ddd;
}
.error{
  display: inline-block;
  margin-top: 8px;
  padding: 8px 12px;
  border-radius: 6px;
  background: var(--brand_color);
  color: #000;
  font-weight: 700;
}
.product_item{
  background: #fff;
  border: 1px solid #eee;
  border-radius: 12px;
  padding: 14px;
  margin-bottom: 18px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.06);
}
.product_item p{
  font-size: 14px;
  margin: 6px 0;
}
.product_item img{
  border-radius: 10px;
  object-fit: cover;
}
.btn{
  display: inline-block;
  padding: 10px 14px;
  border-radius: 8px;
  border: 1px solid #ddd;
  background: #f7f7f7;
  color: #333;
  cursor: pointer;
  margin-right: 8px;
  transition: all .2s ease;
}
.btn:hover{
  background: #333;
  color: #fff;
  border-color: #333;
}
.input_item .submit_btn{
  width: 98%;
  height: 56px;
  margin: 20px auto 30px;
  padding: 0 16px;
  border: none;
  background: var(--brand_color);
  color: #333;
  font-size: 18px;
  font-weight: 700;
  border-radius: 8px;
  cursor: pointer;
  transition: all .2s ease;
}
.input_item .submit_btn:hover{
  background: var(--brand_color_hover);
  box-shadow: 0 6px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}
