@charset "UTF-8";

:root{
  --base_color: #333;
  --base_font_size: 12px;
  --brand_color: #FFD400;
  --brand_color_hover: #E5C200;
}
/* 기본 서식 */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ol,ul,li{
  list-style: none;
}
body{
  font-family: '맑은 고딕', Arial, sans-serif;
  font-size: var(--base_font_size);
  color: var(--base_color);
}
a{
  text-decoration: none;
  color: var(--base_color); 
}
